body::-webkit-scrollbar {
  display: none;
}

canvas{
  position: absolute;
  margin: 0px;
  top:0px;
  left: 0px;
  padding: 0px;
  z-index: -10;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  --color: #00ffdd;
  color: transparent;
  text-shadow: 0 0 1px rgba(0,255,255,1);
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.App {
  font-size: 2rem;
  text-align: center;
  /*font-family: serif;*/
  font-family: 'Ricty Diminished', 'Monaco', 'Consolas', 'Courier New', Courier, monospace, sans-serif;
}
.Head {
  font-size: 3rem;
  padding: 30px;
  padding-top: 0px;
  padding-bottom:0px;
  margin: 4rem;
  width:fit-content;
  margin-left: auto;
  margin-right: auto;
/*  font-family: 'Raleway', sans-serif;*/
  
  /*border: solid 1px;
  border-color: black;*/
/*  animation: 3s 0s 1 cubic-bezier(0.4, 4.88, 0.75, -1.85) Damping,
*/
  animation: 6s 0s 1 ease-in showUp,
  blink infinite 1.9s 6s cubic-bezier(0.1, -10, 0.8, -10) alternate-reverse;
}

.App>hr {
  margin-top: 0px;
  width: 90%;
  padding-top: 0px;
  margin-left: 0px;
  padding-left: 0px;
  text-align: left;
}

.list-element>hr{
  margin-top:0px;
  margin-left: 0px;
  padding-left: 0px;
  width:80%;
  text-align: left;
}

@keyframes showUp{
    0% {
        text-shadow:0 0 50px rgba(0,255,255,1.0);
    }100%{
        text-shadow:0 0 5px rgba(0,255,255,1.0);
    }
}

@keyframes Damping {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateX(0);
  }
}


@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

div.list-element {
  margin-bottom: 30px;
  margin-left:0px;
  animation: 6s ease-in 0s 1 showUp,
  blink_clearly infinite 1.3s 6s cubic-bezier(0.1, -10, 0.8, -10) alternate-reverse;
}

@keyframes inflate{
  0%{
    height: 0px;
    opacity: 0;
  }
  100%{
    height: 500px;
    opacity: 1;
  }
}
@keyframes deflate{
  0%{
    height: 400px;
    opacity: 1;
  }
  100%{
    height: 0px;
    opacity: 0;
  }
}

.problem-sentences-inflate{
  font-size: 1rem;
  margin-top: 0px;
  margin-bottom: 0px;
  animation: 1s 0s 1 inflate ease-out both;
}

.problem-sentences-deflate{
  pointer-events: none;
  font-size: 1rem;
  margin-top: 0px;
  margin-bottom: 0px;
  height: 100px;
  animation: 1s 0s 1 deflate ease-out both;
}

.list-element>hr{
  animation: 3s 0s 1 slideInFromLeft ease-out;
}

button,input{
  z-index: 10;
}

@keyframes Shiver{
  0%{
    transform: rotate(0.00325turn);
  } 50%{
    transform: rotate(-0.00325turn);
  }
  100%{
    transform: rotate(0.00325turn);
  }
}

input{
  --color: #00ffdd;
  color: transparent;
  text-shadow: 0 0 1px rgba(0,255,255,1);

  border: solid 1px;
  background-color: rgba(0,255,240, 0);
  border-color: black;
  font-size: 2rem;
  padding: 0.5rem;
  width: 70%;
  margin-top:3rem;
  margin-bottom:3rem;
  text-align: center;
  font-family: 'Ricty Diminished', 'Monaco', 'Consolas', 'Courier New', Courier, monospace, sans-serif;
}

input:focus{
  border: solid 1px;
  text-align: center;
  border-color: black;
  outline: 0px;
  font-size: 2rem;
  padding: 0.5rem;
  width: 70%;
  margin-top:3rem;
  margin-bottom:3rem;
  animation:.2s 0s infinite linear alternate Shiver;
  font-family: 'Ricty Diminished', 'Monaco', 'Consolas', 'Courier New', Courier, monospace, sans-serif;
}

button{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: solid 0.5px;
  text-align: center;
  border-color: black;
  font-size: 2rem;
  padding: 0.5rem;
/*  background: #FFF;*/
  background-color: rgba(0,255,240, 0);
  color: rgba(0,255,240, 0.5);
  text-shadow: 0 0 10px rgba(0,255,255,0.8);
  animation: blink_clearly infinite 1.3s 1s cubic-bezier(0.1, -10, 0.8, -10) alternate-reverse;
  font-family: 'Ricty Diminished', 'Monaco', 'Consolas', 'Courier New', Courier, monospace, sans-serif;
}

@keyframes blink {
  0%{
    text-shadow: 0 0 4px rgba(0,255,255,1);
  }90%{
    text-shadow: 0 0 4.2px rgba(0,255,255,1);
  }100%{
    text-shadow: 0 0 4.5px rgba(0,255,255,1);
  }
}

@keyframes blink_clearly {
  0%{
    text-shadow: 0 0 1.68px rgba(0,255,255,1);
  }90%{
    text-shadow: 0 0 1.72px rgba(0,255,255,1);
  }100%{
    text-shadow: 0 0 1.79px rgba(0,255,255,1);
  }
}

.container {
  --color: #00ffdd;
  color: transparent;
  text-shadow: 0 0 1px rgba(0,255,255,1);
  font-size: 3rem;
  display: flex;
/*  font-family: Georgia, 'Times New Roman', Times, serif;*/
  font-family: 'Ricty Diminished', 'Monaco', 'Consolas', 'Courier New', Courier, monospace, sans-serif;

  position: fixed;
  margin-top: 3rem;
  width: 100vw;
  text-align: center;
  top:0px;
  flex-direction: column;
  animation: blink infinite 1.9s 1.1s cubic-bezier(0.1, -10, 0.8, -10) alternate-reverse;
}

.problem{
  font-size: 1.3rem;
  margin: 20vw;
  margin: 0px;
  color: transparent;
  text-shadow: 0 0 0.08rem rgba(0,255,255,1);
  animation: blink_clearly infinite 1.3s 1s cubic-bezier(0.1, -10, 0.8, -10) alternate-reverse;
}
